import React, { Component } from 'react';
// import 'node_modules/react-bootstrap-table/dist/react-bootstrap-table-all.min.css';
// import { TableContainer, Table } from 'react-custom-table';

import "./page.css";

import axios from 'axios';


const service = "http://120.79.182.85:3000/entry_form/user_list";

const style = {
    table: {
        "textAlign": "left",

        "width": "50%",
        "margin": "0 auto"
    },
    huge : {
        "width": "100%"
    }
}



class Page extends Component {


    state = {
        info: {
            cell: "Hello"
        },


        data: {

        }
    }

    componentDidMount() {
        let index = parseInt(this.props.match.params.id);
        
        axios.get(service).then((res) => {
            let data = (res.data)[index];
            this.setState({ data:  data});
        }).catch((failed) => {
            // alert(failed);
            let data = require("./config/service.json")[index];
            this.setState({ data: data });
            console.log(this.state.data);
        });
    }


    render() {
        console.log("eH");
        return (
            <section style={style.table}>
                <table >
                    <tbody>
                        <tr >
                            <th>个人信息</th>
                        </tr>
                        <tr>
                            <th>姓名</th>
                            <td>{this.state.data.name}</td>
                            <th>加权</th>
                            <td>{this.state.data.grade}</td>
                        </tr>
                        <tr>
                            <th>院系</th>
                            <td>{this.state.data.profession}</td>
                            <th>专业班级</th>
                            <td>xxx</td>
                        </tr>
                        <tr>
                            <th>E-mail： </th>
                            <td>{this.state.data.email}</td>
                            <th>电话</th>
                            <td>{this.state.data.phone}</td>
                        </tr>

                        <tr>
                            <th>获取方式:</th>
                            <td>{this.state.data.known}</td>
                            <th>是否参加宣讲:</th>
                            <td>{this.state.data.isJoin}</td>
                        </tr>
                    </tbody>
                </table>
                <table>
                    <tbody>
                        <tr>
                            <th>主动实践经历（包括参加过的团队或活动、完成的作品、擅长的技术方向等）</th>
                        </tr>
                        <tr>
                            <td>{this.state.data.practice}</td>
                        </tr>
                        <tr>

                            <th> 加入种子班的目的和个人规划</th>
                        </tr>
                        <tr>
                            <td>{this.state.data.introduce}</td>
                        </tr>
                    </tbody>
                </table>
            </section>

        );
    }
}

export default Page;







