import React, { Component } from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import './list.css';
import { Link } from 'react-router-dom'
import axios from 'axios';


const service = "http://120.79.182.85:3000/entry_form/user_list";

const Logo = require("./logo.svg");

const style = {

    "eye": {
        "width": "50px",
        "height":"50px"
    },
    "row": {
        "display": "flex",
        "justifyContent": "space-around",
    }
}

class ListInfo extends Component {

    state = {
        data: [{
            "name": "",
            "profession": ""
        }]
    }
    infoDetail(index, event) {
        console.log("jump to "+ index);
    }


    componentWillMount() {
        axios.get(service).then((res) => {
            this.setState({ data: res.data });
        }).catch((failed) => {
            // alert(failed);
            let data = require("./config/service.json");
            this.setState({ data: data });
            console.log(this.state.data);
        });
    }

    render() {
        const listData = this.state.data;
        console.log(this.state.data);
        let list = listData.map((item, index) => {
            return (
                    <ListItem  style={style.row} key={index}>
                        <ListItemText>姓名:{item.name}</ListItemText>
                        <ListItemText>加权:{item.grade}</ListItemText>
                        <ListItemText>专业:{item.profession}</ListItemText>
                        <ListItemText>学号:{item.id}</ListItemText>
                        <ListItemText>是否参加例会:{item.isJoin}</ListItemText>
                        <ListItemText>了解途径:{item.known}</ListItemText>
                        <Link to={{ pathname: "/detail/"+index }}>
                            <img src={Logo} style={style.eye} onClick={this.infoDetail.bind(this, index)}/>
                        </Link>

                    </ListItem>
                
            )
        })
        return (
            <section>
                <List>
                    {list}
                </List>
            </section>
        )
    }
}

export default ListInfo;


