import React, { Component } from 'react';
import logo from './logo.svg';
import Page from './page.jsx';
import ListInfo from './list.jsx';
import { Switch, Route } from 'react-router-dom';
import './App.css';

class App extends Component {
  render() {
    return (
      //  <Page/>
      <main>
        <Switch>
          <Route exact path="/" component={ListInfo}>
          </Route>
          <Route path="/detail/:id" component={Page}></Route>
          
        </Switch>
      </main>

      // <ListInfo/>
    );
  }
}

export default App;



